<!-- mapbox -->
<template>
  <div id="map">
    <div>
      <el-radio-group v-model="currentMap" size="small" @change="handleSelect">
        <el-radio-button label="streets-v11">街道图</el-radio-button>
        <el-radio-button label="satellite-v9">卫星图</el-radio-button>
      </el-radio-group>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentMap: 'streets-v11',
      map: '',
      GeoMap: this.$data.mapJson
    }
  },
  components: {},
  created () {
    this.$mapboxgl.setRTLTextPlugin(
      'https://api.mapbox.com/mapbox-gl-js/plugins/mapbox-gl-rtl-text/v0.1.0/mapbox-gl-rtl-text.js'
    )
    this.map.addControl(
      new this.$mapboxgl.MapboxLanguage({
        defaultLanguage: 'en'
      })
    )
  },
  mounted () {
    this.DB.DataBase.get('35').then(doc => {
      // handle doc
      console.log(doc)
      this.initmap(doc)
    }).catch(function (err) {
      console.log(err)
    })
    // this.initmap()
  },
  computed: {},
  methods: {
    initmap (jsonMap) {
      console.log('initmao')
      // var jsonMap = JSON.parse(sessionStorage.getItem('mapJson'))
      this.$mapboxgl.accessToken =
          'pk.eyJ1Ijoid2VpaGFuZzEiLCJhIjoiY2tvM3M4MDZ6MDg2azJ3b2FvM3FrZ2hmeiJ9.fHmcUQQlAnHZOuD-apa7Jw'
      this.map = new this.$mapboxgl.Map({
        container: 'map',
        style: 'mapbox://styles/mapbox/dark-v10',
        // center: [118.3008,25.9277],
        center: jsonMap.cp,
        zoom: 6
      })

      // 注册点击事件
      this.map.on('click', function () {
        console.log('点击')
      })

      this.map.on('load', function () {
        this.addSource('fillSourceID', {
          type: 'geojson',
          // data: getGeoJson()
          // data: '/static/pro35.json'
          // data: '/static/test_goe.json'
          data: jsonMap
        })
        // add heatmap layer here
        this.addLayer({
          id: 'filledID',
          type: 'fill',
          source: 'fillSourceID',
          paint: {
            'fill-color':
                {
                  property: 'confirmed', // this will be your density property form you geojson
                  stops: [
                    [0, '#ffd0a6'],
                    [10, '#ffd0a6'],
                    [100, '#ffaa7f'],
                    [500, '#ff704e'],
                    [1000, '#f04040'],
                    [10000, '#b50a09']
                  ]
                },
            'fill-opacity': 1 /* 透明度 */
          }
        },
        'waterway-label'
        )
      })
    },
    handleSelect (item) {
      this.map.setStyle('mapbox://styles/mapbox/' + item)
    }
  }}
</script>

<style>
  #map {
    height: 800px;
    width: auto;
    border: 1px solid red;
  }
</style>



